/**
 * API Client for OSlash Local Server Communication
 * 
 * Direct HTTP/WebSocket client for the standalone web dashboard.
 */

import type { ServerStatus, SearchResponse, ConfigResponse, ChatMessage } from "./types";

// Configuration - use relative URLs since we're served from the same origin
// or proxied during development
const getBaseUrl = (): string => {
  // In production, we're served from FastAPI, so use relative URLs
  // In development, Vite proxies /api to localhost:8000
  return "";
};

const HEALTH_CHECK_TIMEOUT = 2000;
const REQUEST_TIMEOUT = 30000;

/**
 * API Client class for communicating with the OSlash Local server.
 */
class ApiClient {
  private ws: WebSocket | null = null;
  private reconnectAttempts = 0;
  private maxReconnectAttempts = 3;

  /**
   * Check if the server is healthy and reachable.
   */
  async checkHealth(): Promise<boolean> {
    try {
      const controller = new AbortController();
      const timeoutId = setTimeout(() => controller.abort(), HEALTH_CHECK_TIMEOUT);

      const res = await fetch(`${getBaseUrl()}/health`, {
        method: "GET",
        signal: controller.signal,
      });

      clearTimeout(timeoutId);
      return res.ok;
    } catch {
      return false;
    }
  }

  /**
   * Get server status including connected accounts and document counts.
   */
  async getStatus(): Promise<ServerStatus> {
    const res = await this.fetch("/api/v1/status");
    return res.json();
  }

  /**
   * Get configuration status.
   */
  async getConfig(): Promise<ConfigResponse> {
    const res = await this.fetch("/api/v1/config");
    return res.json();
  }

  /**
   * Perform a search query.
   */
  async search(query: string, options?: {
    sources?: string[];
    limit?: number;
  }): Promise<SearchResponse> {
    const res = await this.fetch("/api/v1/search/", {
      method: "POST",
      body: JSON.stringify({
        query,
        sources: options?.sources,
        limit: options?.limit ?? 10,
      }),
    });
    return res.json();
  }

  /**
   * Trigger sync for all connected sources.
   */
  async syncAll(full: boolean = false): Promise<void> {
    await this.fetch(`/api/v1/sync?full=${full}`, {
      method: "POST",
    });
  }

  /**
   * Trigger sync for a specific source.
   */
  async syncSource(source: string, full: boolean = false): Promise<void> {
    await this.fetch(`/api/v1/sync/${source}?full=${full}`, {
      method: "POST",
    });
  }

  /**
   * Initiate OAuth connection for a source.
   */
  async connectSource(source: string): Promise<{ auth_url: string }> {
    const res = await this.fetch(`/api/v1/auth/${source}/url`, {
      method: "GET",
    });
    const data = await res.json();
    return { auth_url: data.url };
  }

  /**
   * Disconnect a source.
   */
  async disconnectSource(source: string): Promise<void> {
    await this.fetch(`/api/v1/auth/${source}/disconnect`, {
      method: "DELETE",
    });
  }

  /**
   * Connect to WebSocket for chat streaming.
   */
  connectChat(
    sessionId: string,
    callbacks: {
      onStart?: () => void;
      onToken?: (token: string) => void;
      onSources?: (sources: string[]) => void;
      onEnd?: () => void;
      onError?: (error: string) => void;
      onClose?: () => void;
    }
  ): void {
    const wsProtocol = window.location.protocol === "https:" ? "wss:" : "ws:";
    const wsHost = window.location.host;
    this.ws = new WebSocket(`${wsProtocol}//${wsHost}/ws/chat/${sessionId}`);

    this.ws.onopen = () => {
      this.reconnectAttempts = 0;
      console.log("[OSlash] WebSocket connected");
    };

    this.ws.onmessage = (event) => {
      try {
        const data: ChatMessage = JSON.parse(event.data);

        switch (data.type) {
          case "start":
            callbacks.onStart?.();
            break;
          case "token":
            if (data.content) {
              callbacks.onToken?.(data.content);
            }
            break;
          case "sources":
            if (data.sources) {
              callbacks.onSources?.(data.sources);
            }
            break;
          case "end":
            callbacks.onEnd?.();
            break;
          case "error":
            callbacks.onError?.(data.content || "Unknown error");
            break;
        }
      } catch (e) {
        console.error("[OSlash] Failed to parse WebSocket message:", e);
      }
    };

    this.ws.onerror = (error) => {
      console.error("[OSlash] WebSocket error:", error);
      callbacks.onError?.("WebSocket connection error");
    };

    this.ws.onclose = () => {
      console.log("[OSlash] WebSocket closed");
      callbacks.onClose?.();

      // Attempt reconnect
      if (this.reconnectAttempts < this.maxReconnectAttempts) {
        this.reconnectAttempts++;
        setTimeout(() => {
          console.log(`[OSlash] Reconnecting... (attempt ${this.reconnectAttempts})`);
          this.connectChat(sessionId, callbacks);
        }, 1000 * this.reconnectAttempts);
      }
    };
  }

  /**
   * Send a chat message through WebSocket.
   */
  sendChatMessage(content: string): boolean {
    if (this.ws?.readyState === WebSocket.OPEN) {
      this.ws.send(JSON.stringify({
        type: "question",
        content,
      }));
      return true;
    }
    return false;
  }

  /**
   * Disconnect the WebSocket chat connection.
   */
  disconnectChat(): void {
    if (this.ws) {
      this.ws.close();
      this.ws = null;
    }
    this.reconnectAttempts = this.maxReconnectAttempts;
  }

  /**
   * Internal fetch wrapper with timeout and error handling.
   */
  private async fetch(
    path: string,
    options: RequestInit = {}
  ): Promise<Response> {
    const controller = new AbortController();
    const timeoutId = setTimeout(() => controller.abort(), REQUEST_TIMEOUT);

    try {
      const res = await fetch(`${getBaseUrl()}${path}`, {
        ...options,
        signal: controller.signal,
        headers: {
          "Content-Type": "application/json",
          ...options.headers,
        },
      });

      clearTimeout(timeoutId);

      if (!res.ok) {
        const error = await res.json().catch(() => ({ detail: res.statusText }));
        throw new Error(error.detail || `HTTP ${res.status}`);
      }

      return res;
    } catch (error) {
      clearTimeout(timeoutId);
      if (error instanceof Error && error.name === "AbortError") {
        throw new Error("Request timeout");
      }
      throw error;
    }
  }
}

// Export singleton instance
export const api = new ApiClient();

